// Cuando el documento este cargado llamara a la funcion'iniciarApp'
document.addEventListener('DOMContentLoaded', function(){
  iniciarApp();
});


// Como van a ser varias funciones las que se mandaran a llamar cuando se termina de cargar la pagina, con esta funcion es mas que suficiente para solo mandar a llamar una sola funcion
function iniciarApp() {
  navegacionFija();
  crearGaleria();
  scrollNav();
}

function navegacionFija() {
  const barra = document.querySelector('.header');
  const sobreFestival = document.querySelector('.sobre-festival');

  const body = document.querySelector('body');

  /* Como ver que ya di scroll hasta ese elemento*/
  window.addEventListener('scroll', function(e) {
    /* console.log( sobreFestival.getBoundingClientRect() ); */

    if ( sobreFestival.getBoundingClientRect().top < 0 ) {
      /* console.log('ya paase el elemento'); */
      barra.classList.add('fijo');
      body.classList.add('body-scroll');/* Arreglamos movimiento del contenido del body cuando se da scroll, ver scss de globales y scss de header */
    } else {
      barra.classList.remove('fijo');
      body.classList.remove('body-scroll');/* Arreglamos movimiento del contenido del body cuando se da scroll, ver scss de globales y scss de header */
    }
  });
}

function scrollNav() {
  const enlaces = document.querySelectorAll('.navegacion-principal a');

  enlaces.forEach( enlace => {
    enlace.addEventListener('click', function (e) {
      /* Con el preventDefault quitamos la rapidez de ir a la seccion deseada del HTML */
      /** RECUERDA DEJAR LA PAGINA HASTA INDEX.HTML PARA QUE FUNCIONE */
      e.preventDefault();

      /* console.log(e.target.attributes.href.value); */

      const seccionScroll = e.target.attributes.href.value;
      const seccion = document.querySelector(seccionScroll);
      /* seccion.scrollIntoView(); */

      seccion.scrollIntoView( { behavior: "smooth"});

    })
  });
}

function crearGaleria() {
  const galeria = document.querySelector('.galeria-imagenes');

  for(let i = 1; i <= 12; i++) {

    /* Paso 1 */
    /* console.log(i); */

    /* Paso 2 */
    /* const imagen = document.createElement('picture');

    console.log(imagen); */

    const imagen = document.createElement('PICTURE');
    imagen.innerHTML = `
      <source srcset="./build/img/thumb/${i}.avif" type="image/avif">
      <source srcset="./build/img/thumb/${i}.webp" type="image/webp">
      <img loading="lazy" width="200" height="300" src="./build/img/thumb/${i}.jpg" alt="Imagen Galeria">
    `;

    /* console.log(imagen); */

    // Detectar a que imagen le estan dando click, continua con la funcion a que se le llama
    // Es importante que esto se haga como un callback, ya que si se hace de forma sencilla 'imagen.onclick = mostrarImagen(i);' va a mostrar todas las imagenes con los id en consola y eso no es lo que se busca, se busca que cuadno se da click en alguna de ellas, nos indique a que imagen se le esta haciendo click
    imagen.onclick = function() {
      mostrarImagen(i);
    }

    galeria.appendChild(imagen);
  }
}

function mostrarImagen(id) {
  /* console.log('mostrando...', id); */

  // Cuando le den click, creara un elemento 'picture' que contendra la imagen pero en version grande
  const imagen = document.createElement('PICTURE');
  imagen.innerHTML = `
    <source srcset="./build/img/grande/${id}.avif" type="image/avif">
    <source srcset="./build/img/grande/${id}.webp" type="image/webp">
    <img loading="lazy" width="200" height="300" src="./build/img/grande/${id}.jpg" alt="Imagen Galeria">
  `;

  /* Crea el overlay con la imagen */
  const overlay = document.createElement('DIV'); // Se crea un div
  overlay.appendChild(imagen); // Al div se le agrega al picture que ya se habia creado con anterioridad
  overlay.classList.add('overlay'); // A ese div se le añade una clase llamada 'overlay'
  /*Dar click alrededor para quitar la imagen */
  overlay.onclick = function() {
    const body = document.querySelector('body');
    body.classList.remove('fijar-body');
    overlay.remove();
  }

  /* Boton para cerrar el Modal */
  const cerrarModal = document.createElement('P');
  cerrarModal.textContent = 'X';
  cerrarModal.classList.add('btn-cerrar');
  cerrarModal.onclick = function() {
    const body = document.querySelector('body');
    body.classList.remove('fijar-body');
    overlay.remove();
  }
  overlay.appendChild(cerrarModal);

  /* Añadirlo al HTML */
  const body = document.querySelector('body'); // Seleccionamos a la etiqueta body
  body.appendChild(overlay); // AL final del body se agrega el div que creamos

  /* Para evitar dar scroll */
  body.classList.add('fijar-body');
}